* {
  font-family: Arial, Helvetica, sans-serif;
}

html,
body {
  margin: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
}

.start {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
}

.instart {
  margin: 10px;
  font-size: 1.5em;
}

.instart2 {
  margin: 20px;
  font-size: 3em;
}

.todo {
  width: 100%;
  height: 100%;
  padding: 10px;
}

.todo_input {
  padding: 15px;
  display: flex;
  width: 300px;
  align-content: space-around;
  flex-direction: column;
  border: 2px solid black;
}

.todo_text {
  margin: 10px;
}

.todo_add {
  margin: 10px;
}

.name {
  margin: 10px;
}

.clear {
  margin: 10px;
}

.done {
  text-decoration: line-through;
}

li {
  width: 50px;
}